.container {
    display: flex;
    justify-content: center;
    align-items: center;
}

body {
    overflow: hidden;
}

.home-image {
    width: 100vw;
    height: auto;
    position: relative;
}

.name {
    position: absolute;
    text-align: center;
    color: white;
    margin-bottom: 15rem;
}

.name > h1 {
    font-size: 3rem;
    font-weight: 500;
    margin-bottom: 2rem;
}

.name > h2 {
    font-size: 2rem;
    font-weight: 500;
    margin-top: 2rem;
}
