body {
    width: 100vw;
    height: 100vh;
    background: linear-gradient(
        -45deg,
        deepskyblue,
        deepskyblue,
        mediumblue,
        indigo,
        indigo
    );
    background-size: 400% 400%;
    animation: gradient 15s ease infinite;
}

@keyframes gradient {
    0% {
        background-position: 0% 50%;
    }
    50% {
        background-position: 100% 50%;
    }
    100% {
        background-position: 0% 50%;
    }
}

.Projects > h1 {
    color: white;
    text-align: center;
    padding-top: 1.5rem;
}

.project-container {
    display: flex;
    justify-content: center;
    align-items: center;
    padding-top: 2rem;
}

.grid-container {
    display: grid;
    grid-template: auto auto / repeat(2, 1fr);
    gap: 1.5rem;
}

.project {
    color: white;
    text-align: center;
    text-decoration: none;
    font-size: 1.25rem;
}

.image--quizzical {
    width: 12rem;
    border-radius: 0.5rem;
}

.pomodoro-timer {
    display: flex;
    justify-content: center;
    align-items: flex-end;
}

.image--pomodoro-timer {
    width: 20rem;
    border-radius: 0.5rem;
}

.image--react-calculator {
    width: 10rem;
    border-radius: 0.5rem;
}

.drum-machine {
    display: flex;
    justify-content: center;
    align-items: flex-end;
}

.image--drum-machine {
    width: 20rem;
    border-radius: 0.5rem;
}
